// export default LoadingScreen;

import React from "react";
import "../assets/css/loadingStyle.css";
import { Icon } from "@iconify/react";

function LoadingScreen(props) {
  return (
    <body>
      <div className="mContainer">
        <div
          className="loadingContainer fade-in"
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <div>
            <img className="bouncy-jump1"
              src={require("../assets/ai_samrtbin.jpeg")}
              alt=""
              style={{ height: "210px" }}
            />
          </div>
          <div>
            <img className="bouncy-jump2"
              src={require("../assets/circleEconomy.png")}
              alt=""
              style={{ height: "210px" }}
            />
          </div>
          <div>
            <img className="bouncy-jump3"
              src={require("../assets/netZero.png")}
              alt=""
              style={{ height: "210px" }}
            />
          </div>
        </div>
        <div
          style={{
            textAlign: "center",
            marginTop: "20px",
            color: "#000000",
            fontSize: "3rem",
          }}
        >
          <Icon icon="svg-spinners:bars-scale" />
        </div>
      </div>
    </body>
  );
}

export default LoadingScreen;
