// Header.js

import React, {useState, useEffect} from 'react';
import { Icon } from '@iconify/react';



const StatusBarHeader = () => {
  const [time, setTime] = useState(new Date());
  useEffect(() => {
    const intervalId = setInterval(() => {
      setTime(new Date());
    }, 1000);

    // Clear the interval on component unmount
    return () => clearInterval(intervalId);
  }, []);

  const formatTime = (date) => {
    const hours = date.getHours() % 12;
    const minutes = date.getMinutes();
    // const seconds = date.getSeconds();
    const ampm = date.getHours() >= 12 ? "PM" : "AM";

    return `${hours}:${minutes < 10 ? "0" : ""}${minutes} ${ampm}`;
  };
  const headerStyle = {
    height: '70px',
    backgroundColor: 'black',
    display: 'flex',
    justifyContent: 'flex-end',
    alignItems: 'center',
    padding: '0 10px',
     // Adjust padding as needed
  };

  const imageStyle = {
    width: '40px', // Adjust image width as needed
    height: '40px', // Adjust image height as needed
    margin: '10px', // Adjust margin between images as needed
    color: '#699BF7',
  };

  return (
    <div style={headerStyle}>
        <div style={{color:'#ffffff'}}>
        {/* {new Date().toLocaleTimeString([], { hour: "2-digit", minute: "2-digit", hour12: false })} */}
        {formatTime(time)}
      </div >
      <div style={{marginRight:15}}>
       <Icon icon="fluent:cellular-4g-20-filled" style={imageStyle} />
       <Icon icon="ooui:network" style={imageStyle} />
       <Icon icon="location-3-line" style={imageStyle} />
       <Icon icon="entypo:battery" style={imageStyle} />  
       </div>    
    </div>
  );
};

export default StatusBarHeader;
