import React, { useState, useEffect, useRef } from "react";
import "./indexx.css";
import "./responsive.css";
import binData from "../Data/binData.json";
import deviceData from "../Data/deviceData.json";
import chartData from "../Data/chartData.json";
// import VideoComponent from "./VideoComponent";
import { Icon } from "@iconify/react";

const DustScreen = () => {
  const [time, setTime] = useState(new Date());
  const [selectedBin, setSelectedBin] = useState(null);
  const [modalPosition, setModalPosition] = useState({ top: 0, left: 0 });

  const [clickedBin, setClickedBin] = useState(null);
  const devicedata = deviceData[0];
  const [showVideo, setShowVideo] = useState(false);

  // const [selectedFile, setSelectedFile] = useState(null);
  // const fileInputRef = useRef(null);

  useEffect(() => {
    const intervalId = setInterval(() => {
      setTime(new Date());
    }, 1000);

    // Clear the interval on component unmount
    return () => clearInterval(intervalId);
  }, []);

  useEffect(() => {
    // Show the video for the first 60 seconds
    const dustScreenTimeout = setTimeout(() => {
      setShowVideo(false); // Hide the video
    }, 5000);

    // Show the video after another 5 seconds
    const videoTimeout = setTimeout(() => {
      setShowVideo(true); // Show the video
    }, 5000); // 5000 milliseconds delay plus the previous 5000

    // Reset state and show DustScreen UI after the video
    const resetTimeout = setTimeout(() => {
      setShowVideo(false); // Hide the video
    }, 5000 + 5000 + 5000); // 5000 milliseconds delay plus the previous 5000

    // Clear timeouts on component unmount
    return () => {
      clearTimeout(dustScreenTimeout);
      clearTimeout(videoTimeout);
      clearTimeout(resetTimeout);
    };
  }, []); // Empty dependency array to run only once on mount

  const handleDetect = async () => {
    // Update binData here
    console.log("Updating Bin Value");
    const updatedBinData = binData.map((bin) => ({
      ...bin,
      detect: false, // Set detect to false for all bins
    }));

    try {
      const response = await fetch("/save_data", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(updatedBinData),
      });

      if (response.ok) {
        console.log("Data saved successfully");
      } else {
        console.error("Failed to save data:", response.statusText);
      }
    } catch (error) {
      console.error("Error saving data:", error);
    }

    console.log("Updated binData:", updatedBinData);
  };

  const handleBinClick = (index, event) => {
    // Set the selected bin index
    setSelectedBin(index);
    setClickedBin(index);

    console.log("ClickedBin value:", clickedBin);

    // Calculate the position of the selected bin
    const { offsetTop, offsetLeft } = event.currentTarget;
    setModalPosition({ top: offsetTop, left: offsetLeft });

    setTimeout(() => {
      console.log("ClickedBin value:", clickedBin);
      // Automatically close the clickedBin after two seconds
      setTimeout(() => {
        console.log("Timeout executed");
        setClickedBin((prevState) => null);
      }, 4000);
    }, 0);
  };

  // const handleModalClose = () => {
  //   // Close the modal by resetting the selected bin
  //   setSelectedBin(null);
  // };

  const formatTime = (date) => {
    const hours = date.getHours() % 12;
    const minutes = date.getMinutes();
    // const seconds = date.getSeconds();
    const ampm = date.getHours() >= 12 ? "PM" : "AM";

    return `${hours}:${minutes < 10 ? "0" : ""}${minutes} ${ampm}`;
  };
  // const zero = 0;
  
  return (
    <body>
      {/* {showVideo ? (
        // Your video component
  <div >

        <VideoComponent />
        </div>
      ) : ( */}

      <div className="container">
        {/* date section */}
        <div className="date-main">
          {/* device and location section */}
          <div className="device-location">
            <div className="deviceid">
              <p
                style={{
                  color: "#ffffff",
                  fontFamily: "Poppins",
                  fontWeight: 600,
                }}
              >
                Device ID :{" "}
              </p>
              <p
                style={{
                  color: "#ffffff",
                  fontFamily: "Poppins",
                  marginLeft: 5,
                  fontWeight: 600,
                }}
              >
                {" "}
                {devicedata.deviceId}
              </p>
            </div>
            <div className="location">
              <p
                style={{
                  color: "#ffffff",
                  fontFamily: "Poppins",
                  marginTop: -5,
                  fontWeight: 600,
                }}
              >
                Location :{" "}
              </p>
              <p
                style={{
                  color: "#ffffff",
                  fontFamily: "Poppins",
                  marginLeft: 5,
                  marginTop: -5,
                  fontWeight: 600,
                }}
              >
                {" "}
                {devicedata.location}
              </p>
            </div>
          </div>

          {/* time section */}
          <div className="time-date" style={{ marginBottom: 100 }}>
            {/* <div className="time">12:30</div>
        
        <div className="date">Monday, July 14</div> */}
            <div>
              {/* {new Date().toLocaleTimeString([], { hour: "2-digit", minute: "2-digit", hour12: false })} */}
              <p
                style={{
                  fontSize: 90,
                  marginBottom: -10,
                  fontFamily: "Poppins",
                  fontWeight: 10,
                }}
              >
                {formatTime(time)}
              </p>
            </div>
            {new Date().toLocaleDateString(undefined, {
              weekday: "long",
              day: "numeric",
              month: "long",
            })}
          </div>
        </div>

        <div className="dustbin-main">
          {binData.map((item, index) => (
            <>
              <div>
                <div style={{ padding: "25px" }}>
                  {item.detect ? (
                    <Icon
                      icon="f7:hand-thumbsup-fill"
                      className="flash"
                      style={{ color: "#AAFF00", fontSize: 80 }}
                      onAnimationEnd={() => {
                        console.log("Animation completed for bin", index);
                        handleDetect();
                      }}
                    />
                  ) : (
                    <div style={{ height: "80px" }}></div>
                  )}
                </div>
                <div
                  className={`box${index + 1}`}
                  key={index}
                  onClick={(event) => handleBinClick(index, event)}
                >
                  {clickedBin !== index && (
                    <div className={`percent-${item.color}`}>
                      {item.percentage}
                    </div>
                  )}
                  {clickedBin === index && (
                    <div className="popup">
                      <form>
                        <div className="levelcontainer">
                          <label htmlFor="name">
                            Level <span>:</span>
                          </label>
                          <input
                            type="text"
                            name="level"
                            value={item.percentage}
                            disabled
                          />
                        </div>
                        <div className="Volumecontainer">
                          <label htmlFor="name">
                            Volume <span>:</span>
                          </label>
                          <input type="text" name="volume" disabled />
                        </div>
                        <div className="weightcontainer">
                          <label htmlFor="name">
                            Weight <span>:</span>
                          </label>
                          <input type="text" name="weight" disabled />
                        </div>
                      </form>
                    </div>
                  )}
                  {/* <div className={`percent-${item.color}`}>{item.percentage}</div> */}
                  <div className={`label-${item.color}`}>{item.label}</div>
                  <div className={`img_${item.color}`}>
                    <img
                      className={`i ${item.color}binn`}
                      src={require(`../assets/new_img/Layer${item.color}.png`)}
                      style={{ height: item.percentage }}
                      alt=""
                    />
                  </div>
                  <div className={`fill-${item.color}`}></div>
                </div>
              </div>
            </>
          ))}
        </div>

        <div className="pieChart-main">
          <div className="chartdata">
            {chartData.map((item) => (
              <div key={item.GraphId} className={`line${item.GraphId}`}>
                {item.GraphMessage}
              </div>
            ))}
          </div>
          <div className="piechart">
            {/* Your pie chart component goes here */}
          </div>
        </div>
      </div>
      {/* )

} */}
    </body>
  );
};

export default DustScreen;
