import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Keyboard from 'react-simple-keyboard';
import 'react-simple-keyboard/build/css/index.css';

function Login(props) {
  const navigate = useNavigate();
  const [userId, setUserId] = useState('');
  const [password, setPassword] = useState('');
  const [showKeyboard, setShowKeyboard] = useState(false);

  const handleLogin = (e) => {
    e.preventDefault();
    console.log('User ID:', userId);
    console.log('Password:', password);
    navigate('/Diagnostic');
    // Add your authentication logic here
  };

  const onKeyPress = (button) => {
    if (button === '{enter}') {
      handleLogin({ preventDefault: () => {} });
    } else {
      // Handle other key presses
      setUserId((prevUserId) => prevUserId + button);
    }
  };

  const toggleKeyboard = () => {
    setShowKeyboard((prev) => !prev);
  };

  return (
    <div className='formMain' style={styles.formMain}>
      <div id="formcontainer" style={styles.formcontainer}>
        <h2 style={styles.heading}>Login</h2>
        <form className='form' onSubmit={handleLogin}>
          <input
            style={styles.input}
            type="text"
            placeholder="User Name"
            id="username"
            value={userId}
            onChange={(e) => setUserId(e.target.value)}
            onClick={toggleKeyboard}
          />
          <br />

          <input
            style={styles.input}
            type="password"
            placeholder="Password"
            id="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            onClick={toggleKeyboard}
          />
          <br />

          <input style={styles.button} type="submit" id='submit' />
        </form>

        {showKeyboard && (
          <Keyboard
            onChange={(input) => setUserId(input)}
            onKeyPress={onKeyPress}
            layout={{
              default: ['1 2 3 4 5 6 7 8 9 0 {bksp}', 'q w e r t y u i o p', 'a s d f g h j k l', '{shift} z x c v b n m {enter}', '.com @ {space}'],
              shift: ['! @ # $ % ^ & * ( ) _ +', 'Q W E R T Y U I O P', 'A S D F G H J K L', '{shift} Z X C V B N M {enter}', '.com @ {space}'],
            }}
          />
        )}
      </div>
    </div>
  );
};
  const img = require('../assets/backGroundImage.png');
  const styles = {
    formMain:{
      // backgroundImage: 'url(../assets/backGroundImage.png)',
      backgroundImage: `url(${img})`,
    height: '450px',
    width: '100%',
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    },
    formcontainer: {
      backgroundColor: 'white',
    padding: '50px',
    textAlign: 'center',
    borderRadius: '20px',
    width: '300px',
    height: '250px',
    },
    heading: {
      margin: '0px 0px 10px 0px'
    },
    input: {
      width: '250px',
      margin: '20px 0px',
      padding: '10px',
      border: '1px solid black'
      
    },
    button: {
      backgroundColor: '#699BF7',
      color: 'white',
      width:'272px',
      padding:'10px',
      cursor: 'pointer',
      marginLeft:'0px',
      marginTop:'20px'
      
    },
  };

export default Login;