import { Icon } from "@iconify/react";
import React, { useState, useEffect } from "react";
import BrandsJsonData from "../Data/brands_data.json";

function BrandsHeader() {
  const [data, setData] = useState([]);

  useEffect(() => {
    setData(BrandsJsonData);
  }, []);

  const getCountByBrand = (brandName) => {
    const brand = data.find((item) => item.brand === brandName);
    return brand ? brand.count : 0;
  };

  const barContainerStyle = {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "#fff",
    position: "sticky",
    bottom: 0, // Change to 'top: 0' if you want it at the top
    width: "100%",
    height: "70px", // Adjust height as needed
    // borderTop: "1px solid #ccc",
    gap:"10px",
    fontSize:"20px",
  };

  const brandItemStyle = {
    display: "flex",
    gap:"10px",
    alignItems: "center",
    margin: "0 10px",
  };

  const brandIconStyle = {
    width: "100px", // Adjust as needed
    height: "auto", // Adjust as needed
  };

  return (
    <div style={{ textAlign: "center" }}>
      <div style={barContainerStyle}>
        <div style={brandItemStyle}>
          <img
            src={require("../assets/coco.png")}
            alt=""
            style={brandIconStyle}
          />
          <p>Coca Cola : {getCountByBrand("coca-cola")}</p>
        </div>
        <div style={brandItemStyle}>
          <img
            src={require("../assets/Sprite.png")}
            alt=""
            style={brandIconStyle}
          />
          <p>Sprite : {getCountByBrand("sprite")}</p>
        </div>
        <div style={brandItemStyle}>
          <img
            src={require("../assets/kL.png")}
            alt=""
            style={brandIconStyle}
          />
          <p>Kinley : {getCountByBrand("kinley")}</p>
        </div>
        <div style={brandItemStyle}>
          <img
            src={require("../assets/Bisleri.png")}
            alt=""
            style={brandIconStyle}
          />
          <p>Bisleri : {getCountByBrand("bisleri")}</p>
        </div>
        <div style={brandItemStyle}>
          <img
            src={require("../assets/pepsi.png")}
            alt=""
            style={brandIconStyle}
          />
          <p>Pepsi : {getCountByBrand("pepsi")}</p>
        </div>
        <div style={brandItemStyle}>
          <Icon
            icon="basil:other-1-outline"
            style={{ color: "#000000", fontSize: 70 }}
          />
          <p>Others : {getCountByBrand("others")}</p>
        </div>
      </div>
    </div>
  );
}

export default BrandsHeader;
