import React, { useState, useEffect } from 'react';
import './App.css';
import Header from './Header/Header';
import DustScreen from './component/DustScreen';
import StatusBarHeader from './Header/StatusBarHeader';
// import FileInput from './component/FileInput';
import Diagnostic from './component/Diagnostic';
import LoadingScreen from './component/LoadingScreen';
import Login from './component/Login';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import BrandsHeader from './Header/brandsHead';

function App() {
  const [loading, setLoading] = useState(true);
  // const [showVideo, setShowVideo] = useState(false);

  useEffect(() => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
    }, 4000);
  }, []);

  return (
    <div className="App">
      {loading ? (
        <LoadingScreen />
      ) : (
        <BrowserRouter>
            <>
              <StatusBarHeader />
              <Header />
              <BrandsHeader />
              <Routes>
                <Route index element={<DustScreen />} />
                <Route path="/DustScreen" element={<DustScreen />} />
                <Route path="/Login" element={<Login />} />
                <Route path="/Diagnostic" element={<Diagnostic />} />
              </Routes>
              </>
        </BrowserRouter>
      )}
    </div>
  );
}

export default App;
